<template>
  <div id="app">
    <NavMobile />
    <div class="navdirect"  :class="{'open':showNav}">
      <div id="nav-icon" v-if="mobileView" @click="showNav = !showNav">
        <div class="hanburger">
          <i class="fas fa-bars"></i>
        </div>
        <div class="icon3">
          <p class="icon2"> <img alt="Vue logo" src="./assets/logo.png" width="27px" />{{ $t("app.title") }}</p>
        </div>
      </div>
      <Nav v-if="!mobileView" />
      <div class="container">
        <router-view />
      </div>
      <Footer />
    </div>
  </div>
</template>
<script>
import Nav from "@/components/Nav"
import Footer from "./components/Footer"
import NavMobile from "./components/Nav_Mobile.vue"
import {
  setDocumentLang,
  setDocumentTitle
} from "@/util/i18n/document" 

export default {
  data: () => {
    return {
      mobileView: true,
      showNav: false
    }

  },
  methods: {
    handleView() {
      this.mobileView = window.innerWidth <= 990;
    }
  },
  components: { 
    Nav, 
    Footer,
    NavMobile
  },
  created() {
    this.handleView();
    window.addEventListener('resize', this.handleView);
  },
  mounted() {
    this.$watch(
      "$i18n.locale",
      (newLocale, oldLocale) => {
        if (newLocale === oldLocale) {
          return
        }
        setDocumentLang(newLocale)
        setDocumentTitle(this.$t("app.title"))
      },
      { immediate: true }
    )
  }
}
</script>
<style>
body {
  margin: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: inherit;
}
#app .container {
  padding: 1rem;
}

#nav-icon{
  padding: 10px 10px 20px;
  margin-right: 10px;
  cursor: pointer;
  background-color: #41AA64;
  width: calc(100% - 0px);
}

.fa-bars{
  font-size: 2rem;
  color:#fff;
}

.navdirect{
  margin:-10px 0px;
  position: absolute;
  top:10px;
  width: calc(100% - 0px);
  height:130vh;
  background-color: #fff;
  transition: 1s transform cubic-bezier(0, .12, .14,1);

}

.open {
  transform: translateX(300px);
}

.icon2{
  color: #fff;
  font-weight: bold;
  font-size:34px;
}

.icon3{
  display: inline-block;
  top: -25px;
  position: absolute;
  width: calc(100% - 20px);
  text-align: center;
}

.hanburger{
  display: inline-block;
}

</style>
