import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import("../views/About.vue"),
  },
  {
    path: "/statistics",
    name: "Statistics",
    component: () =>
      import("../views/Statistics.vue"),
  },
  {
    path: "/goal",
    name: "Goal",
    component: () =>
      import( "../views/Goal.vue"),
  },
  {
    path: "/bmi",
    name: "BMI",
    component: () =>
      import( "../views/BMI-Calculator.vue"),
  },
  {
    path: "/profile",
    name: "Profile",
    component: () =>
      import( "../views/Profile.vue"),
  },
  {
    path: "/help",
    name: "Help",
    component: () =>
      import( "../views/Help.vue"),
  },
  {
    path: "/start",
    name: "Start",
    component: () =>
      import( "../views/Start.vue"),
  },
  {
    path: "/login",
    name: "Login",
    component: () =>
      import( "../views/Login.vue"),
  },
  {
    path: "/register",
    name: "Register",
    component: () =>
      import( "../views/Register.vue"),
  },

  {
    path: "/distance",
    name: "Distance",
    component: () =>
      import( "../views/Distance.vue"),
  },
  {
    path: "/calories",
    name: "Calories",
    component: () =>
      import( "../views/Calories.vue"),
  },



  {
    path: "/duration",
    name: "Duration",
    component: () =>
      import( "../views/Duration.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
