/* eslint-disable */
<template>
    <div class="all">
        <div class="map">
            <Map />
        </div>
        <br>
        <br>
        <div class="button">
            <div class="b1">
                <button @click="startTimer" class="myButton"><i class="fas fa-play"></i> {{ $t("content.start") }}</button>
            </div>
            
            <div class="b2">
                <button @click="stopTimer" class="myButton2"><i class="fas fa-pause"></i> {{ $t("content.pause") }}</button>
            </div>
            <div class="b3">
                <button @click="addTimer" class="myButton3"><i class="far fa-stop-circle"></i> {{ $t("content.stop") }}</button>
            </div>
        </div>
        <br>
        <br>
        <br>
        <div class="grid-container">
            <div class="distance">
                <div class="km">
                    <span>{{distance2}}</span><br>
                    <span class="dkm">{{ $t("content.distance") }}</span>
                </div>
            </div>
            <div class="duration">
                <div class="tim">
                    {{ hours }} : {{ minute }} : {{ second }}
                    <br><span class="dtime">{{ $t("content.duration") }}</span> 
                </div>
            </div>
            <div class="speed">
                <div class="kmh">
                    <span>{{speed2}} </span><br>
                    <span class="skmh">{{ $t("content.speed") }}</span>
                </div>
            </div>
            <div class="kcal">
                <div class="cal">
                    <span>{{calorie2}} </span><br>
                    <span class="kcall">{{ $t("content.kcal") }}</span>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import Map from "./Map"
export default {
    components: {
        Map,
    },
    data() {
        return {
            hours: 0,
            minute: 0,
            second:0,
            calorie:0,
            calorie2:0,
            distance:0,
            distance2:0,
            speed:0,
            speed2:0,
            kcal:null,
            dist:null,
            min:null,
            tobestsd:[],
            tobestss:[],
            tobestsk:[]

            
    };
  },
  methods: {
      startTimer() {
          this.countInterval = setInterval(() => {
              this.second+=1
              this.distance+=(1/40000)
              this.speed=(this.distance * 0.03)/400
              this.calorie+=0.02
              this.calorie2=parseFloat(this.calorie).toFixed(2)
              this.distance2=parseFloat(this.distance).toFixed(2)
              this.speed2=parseFloat(this.speed).toFixed(2)
              if (this.second==60){
                  this.second =0
                  this.minute+=1
                  if(this.minute == 60){
                      this.minute = 0
                      this.hours +=1
                      if(this.hours==60){
                          this.hours =0
                  }
              }
          }
      }, 1000);
    },
    showAlert() {
        var ok ="Ok";
        var ok2="Окей";
        var message3="Your running statistics have been saved. You can consult them in the statistics menu."
        var message2="Ваша статистика бега сохранена. С ними можно ознакомиться в меню статистики.";
        var message="Ihre Laufstatistiken wurden gespeichert. Sie können sie im Statistikmenü konsultieren.";
        var lang = localStorage.getItem('lang') || 'en';

        if(lang =="en"){
            const options = { size: "sm", okLabel:ok}
            this.$dialogs.alert(message3, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })
        }else if(lang =="de"){
            const options = { size: "sm", okLabel:ok}
            this.$dialogs.alert(message, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })

        }else{
            const options = { size: "sm", okLabel:ok2}
            this.$dialogs.alert(message2, options)
            .then(res => {
                console.log(res) // {ok: true|false|undefined}
            })

        }

    },
    stopTimer() {
        clearInterval(this.countInterval);

    },

    addTimer() {
        this.stopTimer();
        this.kcal = this.calorie2
        this.min = (this.minute + (this.hours*60) + (this.second/60)).toFixed(2)
        this.dist = this.distance2
        this.$localStorage.set('kcal', this.kcal)
        this.$localStorage.set('distance', this.dist)
        this.$localStorage.set('min', this.min)
        this.tobestsd.push(parseFloat(this.dist));
        this.$localStorage.set('bestsd', JSON.stringify(this.tobestsd))
        this.tobestss.push(parseFloat(this.min));
        this.$localStorage.set('bestss', JSON.stringify(this.tobestss))
        this.tobestsk.push(parseFloat(this.kcal));
        this.$localStorage.set('bestsk', JSON.stringify(this.tobestsk))
        this.hours=0;
        this.minute=0;
        this.second=0;
        this.calorie=0;
        this.distance=0;
        this.speed=0;
        this.calorie2=0;
        this.distance2=0;
        this.speed2=0;
        this.showAlert();
    }
  },
  beforeDestroy() { 
    this.stopTimer();
  },
};
</script>


<style scoped>

    .map{ 
        text-align: center;
        margin:auto;
    }

    .tim, .km, .kmh, .cal{
      	margin: auto;
      	width: 300px;
      	padding:5px 0;
      	text-align: center;
      	font-size: 2.5em;
      	font-family: digital;
        font-weight: bold;
      	margin-bottom: 10px;
    }

    .dkm, .dtime, .skmh, .kcall{
        font-size:1.5rem;
        font-family: Arial;
        font-weight: normal;
        opacity: 0.7;
    }
      
  
    .grid-container {
        display: grid;
        margin: auto;
        width:60%;
        border-radius: 8px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 0px 0px;
        border: 2px solid black;
        grid-template-areas:
            "duration duration duration"
            "distance speed kcal";
    }
    .distance { grid-area: distance; }
    .duration { grid-area: duration; }
    .speed { grid-area: speed; }
    .kcal { grid-area: kcal; }

    .button{
        text-align: center;
        
    }
    .b1, .b2, .b3{
        display:inline-block;
        padding: 10px;
    }

    .myButton {
	    background-color:#41AA64;
	    border-radius:28px;
	    border:1px solid #18ab29;
	    display:inline-block;
	    cursor:pointer;
	    color:#ffffff;
	    font-family:Arial;
        font-weight: bold;
	    font-size:17px;
	    padding:16px 31px;
    
	    text-decoration:none;
	    text-shadow:0px 1px 0px #2f6627;
    }
    .myButton:hover {
	    background-color:#247C2F;
        transition:0.5s;
    }
    .myButton:active {
	    position:relative;
	    top:1px;
    }


    .myButton2 {
	    background-color:#c47446;
	    border-radius:28px;
	    border:1px solid #c47446;
	    display:inline-block;
	    cursor:pointer;
	    color:#ffffff;
	    font-family:Arial;
        font-weight: bold;
	    font-size:17px;
	    padding:16px 31px;
	    text-decoration:none;
	    text-shadow:0px 1px 0px #664927;
    }
    .myButton2:hover {
	    background-color:#a15d2f;
        transition:0.5s;
    }
    .myButton2:active {
	    position:relative;
	    top:1px;
    }

    .myButton3 {
	    background-color:#d14545;
	    border-radius:28px;
	    border:1px solid #d14545;
	    display:inline-block;
	    cursor:pointer;
	    color:#ffffff;
	    font-family:Arial;
        font-weight: bold;
	    font-size:17px;
	    padding:16px 31px;
	    text-decoration:none;
	    text-shadow:0px 1px 0px #662727;
    }
    .myButton3:hover {
	    background-color:#a12f2f;
        transition:0.5s;
    }
    .myButton3:active {
	    position:relative;
	    top:1px;
    }



</style>