<template>
  <div class="locale-switcher">
    <select v-model="$i18n.locale"  @change="onChange($event)">
      <option value="en">{{ $t("lang.english") }}</option>
      <option value="de">{{ $t("lang.german") }}</option>
      <option value="ru">{{ $t("lang.russian") }}</option>

    </select>
    <div class="select_arrow">
        <i class="fas fa-globe"></i>
    </div>
  </div>
</template>

<script>

export default {

  methods: {
    onChange(event){
       localStorage.setItem('lang', event.target.value);
       window.location.reload();
      
    }
  }
}

</script>

<style scoped>
    .select_arrow {
        position: absolute;
        top: 7px;
        right: 10px;
        pointer-events: none;
        border-width: 8px 5px 0px 5px;
        border-color: #7b7b7b transparent transparent transparent;
    }

    .locale-switcher {
        position: relative;
        display: inline-block;
        
    }
    .locale-switcher select {
        font-family: 'Arial';
        display: inline-block;
        width: 100%;
        cursor: pointer;
        padding: 8px 35px;
        padding-left: 5px;
        outline: 0;
        border: 0px solid #000000;
        border-radius: 12px;
        background: #b2d4b3;
        color: #676767;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
    } 
    .locale-switcher select::-ms-expand {
        display: none;
    }
    .locale-switcher select:hover,
    .locale-switcher select:focus {
        color: #000000;
        background: #ced5cd;
        transition: 0.5s;
    }
    .locale-switcher select:disabled {
        opacity: 0.2;
        pointer-events: none;
    }

    .locale-switcher select:hover ~ .select_arrow,
    .locale-switcher  select:focus ~ .select_arrow {
        border-top-color: #000000;
    }
    .locale-switcher  select:disabled ~ .select_arrow {
        border-top-color: #cccccc;
    }

</style>