/* eslint-disable */
<template>
  <div>
    <GmapMap
      :center="coordinates"
      :zoom="10"
      map-type-id="terrain"
      style="width: 900px; height: 580px; margin: 32px auto"
    >
      <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="center = m.position"
      />
    </GmapMap>
  </div>
</template>

<script>
export default {
  data() {
    return {
      markers: [],
      coordinates: {
        lat: 53.0,
        lng: 9.0,
      },
    };
  },
  mounted() {
    this.geolocate();
  },
  methods: {
    geolocate: function () {
      navigator.geolocation.getCurrentPosition((position) => {
        this.coordinates = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
      });
    },
  },
};
</script>
